<template>
    <div id="tag">
        <Card>
            <p slot="title">全部标签</p>
            <Tag class="tag" v-for="(item,index) in tagData" :color="tagColor[index%4]" :key="index" :name="index" closable @on-close="deletTag">
                {{item.name}}
            </Tag><br>
            <Button class="add" icon="ios-plus-empty" type="dashed" size="small" @click="handleAdd">添加标签</Button>
        </Card>
    </div>  
</template>
    
<script>
export default {
    name: 'tag',
    data () {
        return {
            tagData: [],
            tagColor:['blue','green','red','yellow']
        }
    },
    created () {
        this.getTag();
    },
    methods: {
        deletTag(event,index){
            this.$Modal.warning({               
                content: "确定删除吗？",
                loading: true,
                onOk: () => {
                    this.$http.post('/tag/delTag',{
                        'tagId': this.tagData[index].type
                    }).then(res => {
                        this.$Modal.remove();
                        this.$Message.info('删除成功！');                        
                        this.tagData.splice(index,1);
                    }).catch(err => {
                        his.$Modal.remove();
                        this.$Message.error('删除失败！');
                    })
                }
            });
        },
        handleAdd(){
            this.$Modal.confirm({
                render: (h) => {
                    return h('Input', {
                        props: {
                            value: this.value,
                            autofocus: true,
                            placeholder: '请输入新的标签'
                        },
                        on: {
                            input: (val) => {                                
                                this.value = val;
                            }
                        }                        
                    })
                },
                loading: true,
                onOk: () => {
                    if(this.value == null){
                        this.$Modal.remove();
                        this.$Message.error('标签名不能为空！');
                    }else{
                        this.$http.post('/tag/addTag',{
                            'tagName': this.value
                        }).then(res => {                        
                            this.$Modal.remove();
                            this.getTag();
                            this.$Message.info("添加成功！");                        
                            this.value = "";
                        }).catch(err => {
                            this.$Modal.remove();
                            this.$Message.error("添加失败！");    
                        })
                    }
                },
                onCancel: () => {
                    this.value = ""
                }
            })
        },
        getTag () {
            this.$http.get('/tag/sellectAllTag')
                .then(res => {
                    this.tagData = res.data.msg;
                })
                .catch(err => {
                    this.$Mssage.err("出错啦！！！")
                })
        }
    }
}
</script>
<style lang="scss" scoped>
.tag{
    margin-right: 20px;
}
.add{
    margin-top: 10px;
}
</style>




